* {
  margin: 0;
  padding: 0;
  margin-block-start: 0;
  margin-block-end: 0;
  -webkit-font-smoothing: antialiased;
  box-sizing: border-box;
  font-family: Roboto, sans-serif;
  font-size: 15px;
}

.wrapper {
  max-width: 1200px;
  width: 100%;
  &--head {
    display: flex;
    justify-content: space-between;
  }
}

.title {
  font-size: 33px;
  line-height: 1.3;
  font-weight: bold;
  text-align: center;
}


